.loading-container {
  display: flex;
  width: 100%;
  height: 100%;

  align-items: center;
  justify-content: center;
}

img.loading {
  animation: rotation 4s infinite linear;
  width: auto;
  height: 300px;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
